"use client";

import { useEffect, useState } from "react";

type Todo = {
  _id: string;
  title: string;
  description?: string;
  date: string;
};

export const dynamic = "force-dynamic";

export default function TodoPage() {
  const [todos, setTodos] = useState<Todo[]>([]);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");

  const fetchTodos = async () => {
    try {
      const res = await fetch("http://localhost:5000/api/todos?page=1", {
        cache: "no-store",
      });
      const data = await res.json();
      setTodos(data.todos);
    } catch (err) {
      console.error("Error fetching todos:", err);
    }
  };

  useEffect(() => {
    fetchTodos();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const res = await fetch("http://localhost:5000/api/todos", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ title, description }),
      });

      if (res.ok) {
        setTitle("");
        setDescription("");
        fetchTodos(); // Refresh list
      } else {
        console.error("Failed to add todo");
      }
    } catch (err) {
      console.error("Error:", err);
    }
  };

  return (
    <div className="flex h-screen">
      {/* Left: Sidebar */}
      <div className="w-1/3 bg-white p-6 border-r overflow-y-auto">
        <h1 className="text-2xl font-bold mb-4">To-Do</h1>

        <form onSubmit={handleSubmit} className="space-y-4 mb-6">
          <input
            name="title"
            placeholder="Title"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            className="w-full p-2 border rounded"
          />
          <textarea
            name="description"
            placeholder="Description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            className="w-full p-2 border rounded"
          />
          <button
            type="submit"
            className="bg-blue-500 text-white px-4 py-2 rounded"
          >
            Add Task
          </button>
        </form>

        <div className="space-y-4">
          {todos.map((todo) => (
            <a
              key={todo._id}
              href={`/todo/${todo._id}`}
              className="block p-4 bg-gray-50 rounded shadow"
            >
              <h2 className="font-semibold">{todo.title}</h2>
              <p className="text-sm text-gray-500">
                {new Date(todo.date).toLocaleDateString()}
              </p>
            </a>
          ))}
        </div>
      </div>

      {/* Right: Empty */}
      <div className="flex-1 p-6 text-center text-gray-400">
        <p>Select a To-Do to view</p>
      </div>
    </div>
  );
}
